import React from "react";
import { useState, useEffect } from "react";
import Current from "./Current";
import search from "../img/search.svg";
import Spin from "./Spin";
import moment from "moment";
import {
  GeoapifyGeocoderAutocomplete,
  GeoapifyContext,
} from "@geoapify/react-geocoder-autocomplete";
// import "@geoapify/geocoder-autocomplete/styles/minimal.css";
import "font-awesome/css/font-awesome.min.css";

window.moment = moment;
const Info = () => {
  const [city, setCity] = useState("");
  const [degIn, setdegIn] = useState("C");
  const [loading, setLoading] = useState(true);
  const key = process.env.REACT_APP_API;
  const cityK = process.env.REACT_APP_CITY_API_KEY;
  let cityIn = "katra";
  // const handleChange = (e) => {
  //   cityIn = e.target.value;
  //   // console.log(cityIn);
  // };

  //  let data= document.getElementsByClassName("geoapify-autocomplete-input")[0];
  // console.log(data);
  function onPlaceSelect(value) {
    if (value != null) {
      cityIn = value.properties.city;
      if (cityIn == undefined) {
        cityIn = value.properties.name;
      }
      console.log(cityIn);
      find();
    }
  }
  function onSuggectionChange(value) {
    console.log("suggestion change:");
  }
  let degC = () => {
    // temp=(main.temp-273.15).toFixed(2);
    if (degIn == "F") {
      setMainI(((mainI - 32) * 5) / 9);
      setMainL(((mainL - 32) * 5) / 9);
    }
    setdegIn("C");
  };
  let degF = () => {
    // temp=((main.temp-273.15)*9/5+32).toFixed(2);
    setMainI((mainI * 9) / 5 + 32);
    setMainL((mainL * 9) / 5 + 32);
    setdegIn("F");
  };
  const [main, setMain] = useState({});
  const [mainI, setMainI] = useState(0);
  const [mainL, setMainL] = useState(0);
  const [aqi, setAqi] = useState("");
  const [airD, setairD] = useState({});
  const [icon, setIcon] = useState({});
  const [sun, setSun] = useState({});
  const [wind, setWind] = useState({});
  const [cast, setCast] = useState({});

  let locate = async () => {
    let ip = "https://ipapi.co/json/";
    let dataI = await fetch(ip);
    let parsedDataI = await dataI.json();
    if (dataI.status == "200") {
      cityIn = parsedDataI.city;
    }
    await find();
  };
  function aqiVal(val) {
    if (val == 1) setAqi("Good");
    else if (val == 2) setAqi("Fair");
    else if (val == 3) setAqi("Moderate");
    else if (val == 4) setAqi("Poor");
    else if (val == 5) setAqi("Very Poor");
    else setAqi("Bad Request");
  }
  let find = async () => {
    setLoading(true);
    // console.log(cityIn)
    let url = `https://api.openweathermap.org/data/2.5/weather?q=${cityIn}&appid=${key}`;
    let data = await fetch(url);
    let parsedData = await data.json();
    if (data.status == "200") {
      setMain(parsedData.main);
      let t = parsedData.main;
      setMainI(t.temp - 273.15);
      setMainL(t.feels_like - 273.15);
      setCity(parsedData.name);
      setIcon(parsedData.weather[0]);
      setSun(parsedData.sys);
      setWind(parsedData.wind);
      // console.log(parsedData);
      //air pollution data
      let pol = parsedData.coord;
      let AirUrl = `https://api.openweathermap.org/data/2.5/air_pollution?lat=${pol.lat}&lon=${pol.lon}&appid=${key}`;
      let Airdata = await fetch(AirUrl);
      let AirparsedData = await Airdata.json();
      // console.log(AirparsedData.list[0]);
      let list = AirparsedData.list[0];
      let aqi1 = list.main.aqi;
      aqiVal(aqi1);
      setairD(list.components);
      let forecast = `https://api.openweathermap.org/data/2.5/forecast?q=${cityIn}&appid=${key}`;
      let castdata = await fetch(forecast);
      let castparsedData = await castdata.json();
      console.log(castparsedData);
      setCast(castparsedData);

      setLoading(false);
    } else {
      setLoading(false);
      document.getElementById("wrong").style.display = "block";
      setTimeout(() => {
        document.getElementById("wrong").style.display = "none";
      }, 2000);
    }
  };
  useEffect(() => {
    locate();
  }, []);
  return (
    <>
      <div id="wrong"> Wrong City &nbsp;</div>
        <div>
          <div id="deg">
            <button onClick={degC} className="deg">
              &deg;C
            </button>
            <br />
            <button onClick={degF} id="fdeg" className="deg">
              &deg;F
            </button>
          </div>
          <div id="search">
            {/* <span style={{ color: "white",paddingTop:'3px', fontSize: "18px" }}> City:</span>{" "} */}
            &nbsp;&nbsp;
            {/* <input
              
            /> */}
            <GeoapifyContext apiKey={cityK}>
              <GeoapifyGeocoderAutocomplete
                placeholder="&#xF002;  Search City..."
                type="city"
                lang="en"
                // position={position}
                // countryCodes={countryCodes}
                limit="4"
                // value={this.value}
                placeSelect={onPlaceSelect}
                suggestionsChange={onSuggectionChange}
              />
            </GeoapifyContext>
          </div>
          {/* <br/> */}
          {/* <br/> */}
          {loading ? (<Spin />):(
          <Current
            city={city}
            icon={icon.icon}
            temp={mainI.toFixed(2)}
            degIn={degIn}
            hum={main.humidity}
            fl={mainL.toFixed(2)}
            pre={main.pressure}
            des={icon.description}
            set={window.moment(sun.sunset * 1000).format("hh:mm:ss a")}
            rise={window.moment(sun.sunrise * 1000).format("hh:mm:ss a")}
            deg={wind.deg}
            speed={wind.speed}
            aqi={aqi}
            co={airD.co.toFixed(2)}
            no2={airD.no2.toFixed(2)}
            o3={airD.o3.toFixed(2)}
            so2={airD.so2.toFixed(2)}
            nh3={airD.nh3.toFixed(2)}
            pm2={airD.pm2_5.toFixed(2)}
            pm10={airD.pm10.toFixed(2)}
            cast={cast.list}
          ></Current>)}
        </div>
      
    </>
  );
};

export default Info;
