import React from "react";
import location from "../img/location.svg";
import PropTypes from "prop-types";
import temp from "../img/temperature.png";
import air from "../img/air.png";
import hum from "../img/humidity.png";
import des from "../img/weather.png";
import rise from "../img/sunrise.png";
import set from "../img/sunset.png";
import speed from "../img/speed.png";
import wind from "../img/wind.png";
import so2 from "../img/so2.png";
import no2 from "../img/no2.png";
import o3 from "../img/o3.png";
import co from "../img/co.png";
import pm10 from "../img/pm10.png";
import nh3 from "../img/nh3.png";
import moment from "moment";
import Carousel from 'react-elastic-carousel';

const breakPoints = [
  { width: 1, itemsToShow: 1 },
  { width: 50, itemsToShow: 2, itemsToScroll: 2 },
  { width: 768, itemsToShow: 3 },
  { width: 1200, itemsToShow: 4 }
];
function Current(props) {
  function airB() {
    if (props.aqi == "Good") return "green";
    else if (props.aqi == "Fair") return "yellow";
    else if (props.aqi == "Moderate") return "orange";
    else if (props.aqi == "Poor") return "red";
    else if (props.aqi == "Very Poor") {
      return "purple";
    }
  }
  var elements = document.getElementsByClassName("Pollute");
  for (var i = 0; i < elements.length; i++) {
    if (elements[i].style.width > "100%") elements[i].style.width = "100%";
  }
  let arr = props.cast;
  const fcast = [];
  // const dcast=[];
  let j = 0;
  let z = 0;
  arr.map((e, i) => {
    // e=e.dt;
    if (i == j) {
      let a = moment(e.dt * 1000)
        .calendar()
        .split(" ")[0];
      let b = moment(e.dt * 1000).format("D MMM");
      fcast[z] = a + " - " + b;
      j += 8;
      z++;
    }
    // console.log(dcast)
  });
  let sub = (i) => {
    let subCast = [];
    let index = 0;
    arr.map((e) => {
      // let b = moment(e.dt * 1000)
      //   .calendar()
      //   .split(" ")[0];
      let a = e.dt_txt;
      let c = a.split("-")[2];
      // console.log(c.split(" ")[0]);

      if (c.split(" ")[0] === fcast[i].split(" ")[2]) {
        subCast[index] = e;
        index++;
      }
    });
    console.log(subCast);
    return subCast;
  };

  return (
    <div id="currentI">
      <div id="flexIn">
      <div id="current">
        <div className="lc">
          <img className="Ic" src={location} />
          &nbsp;&nbsp;
          <span style={{ color: "black" }} className="Ich">
            {props.city}
          </span>
        </div>
        <div className="tempDeg">
          <img
            style={{ height: "140px" }}
            src={` https://openweathermap.org/img/wn/${props.icon}@2x.png`}
          />
          <span className="temp">{props.temp}</span>
          <small id="degIn">{props.degIn}</small>
        </div>
        <div className="des">
          <span className="Ich1">{props.des}</span>
        </div>

        <div className="fl">
          <span className="fl1">Feels Like</span>
          <br />
          <img className="Ic2" style={{ width: "20px" }} src={temp} />
          <span className="Ich1">
            {props.fl}
            {props.degIn}
          </span>
        </div>

        <div className="wind">
          <span style={{ marginLeft: "-20px" }}>Wind Speed</span>
          <br></br>
          <img
            className="Ic1"
            style={{
              width: "30px",
              height: "30px",
              marginBottom: "-10px",
              marginTop: "5px",
            }}
            src={speed}
          />
          <span className="Ich1"> {props.speed} m/s</span>
        </div>
        <div className="direct">
          <span>Wind Direction</span>
          <br></br>
          <img
            className="Ic1"
            id="direct1"
            style={{
              transform: `rotate(${props.deg}deg)`,
              width: "65px",
              height: "65px",
              marginBottom: "-10px",
              marginTop: "5px",
            }}
            src={wind}
          />
          <span className="Ich1"> {props.deg}</span>
        </div>

        <div className="pre">
          <span style={{ marginLeft: "-40px" }}>Pressure</span>
          <br></br>
          <img
            className="Ic1"
            style={{
              width: "30px",
              height: "30px",
              marginBottom: "-10px",
              marginTop: "5px",
            }}
            src={air}
          />
          <span className="Ich1"> {props.pre} hPa</span>
        </div>
        <br></br>
        <br></br>
        <br></br>
        <br></br>
        <div className="hum">
          <span>Humidity</span>
          <br />
          <img
            className="Ic2"
            src={hum}
            style={{ width: "18px", height: "18px" }}
          />
          <span className="Ich1"> {props.hum}%</span>
        </div>
      </div>

      <div id="airIndex" style={{ boxShadow: `${airB()} 0.5px 0.5px 2px 2px` }}>
        <span id="index">Air Quality :&nbsp; {props.aqi}</span>
        <hr />
        <div className="flexP">
          <div className="airPollutants">
            <img src={so2} style={{ width: "30px", height: "20px" }} />
            &nbsp;
            <span>
              SO<sub>2</sub>
            </span>
            &nbsp;&nbsp;&nbsp;
            <div
              className="Pollute"
              style={{ width: `${(props.so2 / 350) * 100}% ` }}
            ></div>
            <span id="indexVal">{props.so2} &micro;g/m&#179;</span>
          </div>
          <div className="airPollutants">
            <img
              src={no2}
              style={{
                width: "40px",
                height: "40px",
                marginBottom: "-10px",
                marginTop: "-10px",
              }}
            />
            <span>
              NO<sub>2</sub>
            </span>
            &nbsp;&nbsp;&nbsp;
            <div
              className="Pollute"
              style={{ width: `${(props.no2 / 200) * 100}% ` }}
            ></div>
            <span id="indexVal">{props.no2} &micro;g/m&#179;</span>
          </div>
        </div>
        <div className="flexP">
          <div className="airPollutants">
            <img
              className="Ic2"
              src={pm10}
              style={{ width: "30px", height: "20px" }}
            />
            &nbsp;
            <span>
              PM<sub>10</sub>
            </span>
            &nbsp;&nbsp;&nbsp;
            <div
              className="Pollute"
              style={{ width: `${(props.pm10 / 200) * 100}% ` }}
            ></div>
            <span id="indexVal">{props.pm10} &micro;g/m&#179;</span>
          </div>
          <div className="airPollutants">
            <img
              className="Ic2"
              src={pm10}
              style={{ width: "20px", height: "20px" }}
            />
            &nbsp;
            <span>
              PM<sub>2.5</sub>
            </span>
            &nbsp;&nbsp;&nbsp;
            <div
              className="Pollute"
              style={{ width: `${(props.pm2 / 75) * 100}% ` }}
            ></div>
            <span id="indexVal">{props.pm2} &micro;g/m&#179;</span>
          </div>
        </div>
        <div className="flexP">
          <div className="airPollutants">
            <img
              src={o3}
              style={{
                marginTop: "5px",
                marginBottom: "-5px",
                width: "30px",
                height: "25px",
              }}
            />
            &nbsp;
            <span>
              O<sub>3</sub>
            </span>
            &nbsp;&nbsp;&nbsp;
            <div
              className="Pollute"
              style={{ width: `${(props.o3 / 180) * 100}% ` }}
            ></div>
            <span id="indexVal">{props.o3} &micro;g/m&#179;</span>
          </div>
          <div className="airPollutants">
            <img
              src={co}
              style={{ marginTop: "5px", width: "30px", height: "25px" }}
            />
            &nbsp;
            <span>CO</span>&nbsp;&nbsp;&nbsp;
            <div
              className="Pollute"
              style={{ width: `${(props.co / 15400) * 100}% ` }}
            ></div>
            <span id="indexVal">{props.co} &micro;g/m&#179;</span>
          </div>
        </div>
        <div className="airPollutants">
          <img
            src={nh3}
            style={{ marginTop: "5px", width: "30px", height: "25px" }}
          />
          &nbsp;
          <span>
            NH<sub>3</sub>
          </span>
          &nbsp;&nbsp;&nbsp;
          <span id="indexVal">{props.nh3} &micro;g/m&#179;</span>
        </div>
      </div>

      <div id="Seatime">
        <div className="duration"></div>
        <img src={rise} id="rise" />
        <img src={set} id="set" />
        <div id="rise1">
          <span style={{ textDecoration: "underline" }}>Sunrise</span>
          <br />
          <span
            style={{
              fontFamily:
                "'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif",
            }}
          >
            {props.rise}
          </span>
        </div>
        <div id="set1">
          <span style={{ textDecoration: "underline" }}>Sunset</span>
          <br />
          <span
            style={{
              fontFamily:
                "'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif",
            }}
          >
            {props.set}
          </span>
        </div>
      </div>
      </div>
      <div id="forecast">
        <span>Forecast</span>
        <hr />
        {fcast.map((e, i) => (
          <div className="dayCast" key={i}>
            <span style={{ position: "absolute" }}>{e}</span>
           <Carousel breakPoints={breakPoints}>
            {sub(i).map((e, i) => (
              <div className="subCast" key={i}>
                <span style={{ borderBottom: ".5px wheat solid" }}>
                  {moment(e.dt_txt).format("h a")}
                </span>
                <br />
                <div style={{ display: "flex" }}>
                  <img
                    style={{ height: "40px" }}
                    src={` https://openweathermap.org/img/wn/${e.weather[0].icon}@2x.png`}
                  />
                  <span style={{ marginTop: "8px" }}>
                    {(e.main.temp - 273.15).toFixed(1)}
                    <sup>&deg;C</sup>
                  </span>
                </div>
                <span>{e.weather[0].main}</span>
              </div>
            ))}
           </Carousel>
          </div>
        ))}
      </div>

    </div>
  );
}

export default Current;
