import React from 'react'
import PropTypes from 'prop-types'
import air from '../img/airPollutionI.png'
import forecast from '../img/storm.png'

export default function Navbar(nav) {
    return (
        <nav>
            <span  className='navH'>{nav.header}</span>
            {/* <a className='navA' a='/'>Login / Sign up</a> */}
            <a className='navA' href='#forecast'><img style={{height:'30px'}} src={forecast}/></a>
            <a className='navA' href='#airIndex'><img style={{height:'30px'}} src={air}/></a>
        </nav>
    )
}

Navbar.propTypes = {
    header: PropTypes.string.isRequired
};

Navbar.defaultProps = {
    header: 'Your Title will Come here'
};